.car-player {
    position: absolute;
    bottom: 0px;
    left: 0px;
    height: 16%;
    width: 19%;
    animation-timing-function: cubic-bezier(0.12, 0.7, 0.44, 1.01); 
}

.object1-item {
    position: absolute;
    height: 15%;
    width: 14%;
}

.object1-item > div {
    width: 100%;
    height: 100%;
    background-image: url(../assets/images/game3/object_01.png);
    background-repeat: no-repeat;
    background-size: contain;
}

.object1-item.is-touched > div {
    animation: collision-down 2s;
    animation-timing-function: ease-out;
    animation-iteration-count: 1;
}

