@keyframes collision-down {
    0% { opacity: 1;}
    100% { opacity: 0; }
}

@keyframes jump {
    0% { transform: translate(0px, 0px); }
    100% { transform: translate(0px, -3vh); }
}

@keyframes shown {
    0% { opacity: 0.5; }
    100% { opacity: 1.0; }
}


