@keyframes background-move {
    0% {
        transform: translateX(0); 
    }
    100% {
        transform: translateX(-5%); 
    } 
}

@keyframes add-ten {
    0% {
        opacity: 1.0;
        transform: translate(0px, 0px);
    }
    100% {
        opacity: 0.0;
        transform: translate(-3vh, -3vh);
    }
}

.game-foreground {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 200%;
    height: 100%;
    display: flex;
    z-index: -1;
    background-image: url(../assets/images/game3/bg1.jpg);
    background-repeat: repeat-x;
    background-size: 50% 100%;
}